<template>
  <div class="work-ranking-container">
    <div class="public-card aside-card" style="position: relative;">
      <div class="public-title">
        工作排名
      </div>
      <div class="work-ranking-header">
        <div class="my-tab-header">
          <a href="javascript:;" :class="activeTab == 'city' ? 'active' : ''" class="tab-btn" @click="tabClick('city')">
            一区一档
          </a>
          <a href="javascript:;" :class="activeTab == 'county' ? 'active' : ''" class="tab-btn" @click="tabClick('county')">
            一镇一档
          </a>
        </div>
      </div>
      <!-- 表格 -->
      <CityRanking v-if="activeTab=='city'" :tableData="tableData" />
      <AreaRanking v-if="activeTab=='county'" :tableData="tableData" />

    </div>
  </div>
</template>

<script>
import CityRanking from "./CItyRanking";
import AreaRanking from "./AreaRanking";
import { http } from "@/utils/request";
export default {
  components: {
    CityRanking,
    AreaRanking,
  },
  data() {
    return {
      // 当前tab
      activeTab: "city",
      // 工作排名表格数据
      tableData: [],
    };
  },
  created() {
    // 获取工作排名数据
    this.getWorkDataList(
      "2",
      this.$moment().subtract(1, "days").format("YYYY-MM-DD"),
      this.activeTab
    );
  },
  methods: {
    tabClick(activeTab) {
      this.activeTab = activeTab;
      this.getWorkDataList(
        "2",
        this.$moment().subtract(1, "days").format("YYYY-MM-DD"),
        this.activeTab
      );
    },

    // 获取工作排名数据
    getWorkDataList(time_type, time, rank_type) {
      // time_type: 2: 日 4: 月 5: 年
      // time: 如果是月，是月的第1天。如果是年，是年的第1天
      // rank_type: city: 市 county: 区

      let json = {
        username: "api",
        password: "a123456b",
        time_type: time_type,
        time: time,
        rank_type: rank_type,
      };
      http
        .get(
          "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/ranks",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          if (res.data.code === 2000) {
            this.tableData = res.data.result;
          }
        });
    },
  },
};
</script>

<style lang="scss">
@import "@/styles/common.scss";
.work-ranking-container {
  .public-title {
    position: relative;
  }
  .work-ranking-header {
    position: absolute;
    right: 20px;
    top: 25px;
    .my-tab-header {
      display: flex;
      justify-content: flex-end;
      .tab-btn {
        width: 98px;
        height: 39px;
        line-height: 39px;
        text-align: center;
        background: #2355af;
        font-size: 16px;
        color: #fff4f4;
        &.active {
          background: #59d7ff;
          color: #000;
          font-weight: bold;
        }
      }
    }
  }
}
</style>