<template>
  <div class="city-ranking-container">
    <!-- 表格 -->
    <div class="table-box">
      <div class="my-table-header">
        <span style="text-align:left;width: 250px;">区</span>
        <span style="width: 200px; ">PM2.5</span>
        <span style=" width:100px;">同比 </span>
      </div>
      <div class="my-table-body">
        <vue-seamless-scroll :data="tableData" class="seamless-warp" :class-option="classOption">
          <a href="javascript:;" @click="goToDetails(item)" v-for="(item, index) in tableData" :key="index" class="my-table-content">
            <div style="width: 250px" class="name">{{ item.name }}</div>
            <div style="width: 200px">{{ item.pm25 }}</div>
            <div style="width: 100px">
              <span class="icon-green" v-if="item.pm25_qoq <0"></span>
              <span class="icon-red" v-else-if="item.pm25_qoq >0"></span>
              <span v-else></span>
            </div>
          </a>
        </vue-seamless-scroll>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["tableData"],
  data() {
    return {};
  },
  created() {},
  methods: {
    goToDetails(item) {
      this.$router.push({
        name: "CityRanking",
        query: {
          details: item,
        },
      });
    },
  },
  computed: {
    classOption() {
      return {
        step: 0.5, // 数值越大速度滚动越快
        // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      };
    },
  },
};
</script>

<style lang="scss" >
.city-ranking-container {
  .table-box {
    .my-table-body {
      font-size: 19px;
      max-height: 160px;
      overflow: hidden;
      .my-table-content {
        opacity: 1;
        height: 38px;
        line-height: 38px;
        margin-bottom: 9px;
        background: linear-gradient(
          to right,
          rgba(74, 129, 169, 0) 0%,
          rgba(68, 124, 165, 0.49) 50%,
          rgba(74, 129, 169, 0) 100%
        ) !important;
        .name {
          color: #68b3f3;
          text-align: left;
        }
        .icon-green {
          display: inline-block;
          width: 10px;
          height: 16px;
          background: url("~@/assets/green-arrow.png");
          background-size: contain;
        }
        .icon-red {
          display: inline-block;
          width: 10px;
          height: 16px;
          background: url("~@/assets/red-arrow.png");
          background-size: contain;
        }
      }
    }
  }
}
</style>