<template>
    <div class="early-warning-container">
        <div class="public-card aside-card">
            <div class="public-title">
                今日预警
                <div class="early-warning-header">
                    <div class="box">
                        <span class="number">{{ total }}</span>
                        <span class="title-warning">(件)</span>
                    </div>
                    <div class="box">
                        <span class="title-warning">待反馈</span
                        ><span class="number"> {{ warningNumber }}</span
                        ><span class="title-warning">(件)</span>
                    </div>
                    <div class="box">
                        <span class="title-handle">已反馈</span
                        ><span class="number"> {{ handleNumber }}</span
                        ><span class="title-warning">(件)</span>
                    </div>
                </div>
            </div>
            <!-- 表格 -->
            <div class="table-box">
                <div class="my-table-header">
                    <span style="width: 140px">报警地址</span>
                    <span style="width: 120px">时间</span>
                    <span style="width: 200px">来源</span>
                    <span style="width: 100px">状态</span>
                </div>
                <div class="my-table-body">
                    <vue-seamless-scroll :data="tableData" class="seamless-warp"  :class-option="classOption">
                        <a
                            href="javascript:;"
                            @click="goToDetails(item)"
                            v-for="(item, index) in tableData"
                            :key="index"
                            class="my-table-content"
                        >
                            <div style="width: 140px; white-space: nowrap; overflow: hidden; text-overflow:ellipsis;">{{ item.address }}</div>
                            <div style="width: 120px; white-space: nowrap;">{{ item.occur_datetime_new }}</div>
                            <div style="width: 200px">{{ item.event_type_name }}</div>
                            <div style="width: 100px">
                                <span class="text-red" v-if="item.status_name == '待反馈'">{{item.status_name}}</span>
                                <span class="text-green" v-else-if="item.status_name == '已反馈'">{{item.status_name}}</span>
                            </div>
                        </a>
                    </vue-seamless-scroll>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {http} from '@/utils/request'
export default {
    data(){
        return {
            tableData: [],
            total: 0,
            warningNumber: 0,
            handleNumber: 0,
        }
    },
    mounted() {
        this.requestTableData();
    },
    methods: {
        requestTableData() {
            let daiNum = 0;
            let yiNum = 0;
            let json = {username: "api", password: "a123456b"}
            http.get('https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/events', {params: {json}}).then(res => {
                this.tableData = res.data.result;
                this.total = this.tableData.length;
                this.tableData.forEach(item => {
                    if(item.status_name === '待反馈') daiNum++;
                    if(item.status_name === '已反馈') yiNum++;
                    item.address = item.address.slice(6,item.address.length)
                    item.occur_datetime_new = item.occur_datetime.slice(5,16)
                })
                this.handleNumber = yiNum;
                this.warningNumber = daiNum;

                let newTableData =res.data.result;
                let today = this.$moment(new Date()).format("YYYY-MM-DD 00:00:00");
                let yesterday = this.$moment(new Date())
                .subtract(1, "days")
                .format("YYYY-MM-DD 00:00:00");
                // 两日内待反馈数据 标注
                newTableData = newTableData.filter(item=>{
                    return item.status_name == "待反馈" && (item.occur_datetime == today ||  item.occur_datetime == yesterday)
                })
                let warningData = {
                    newTableData:newTableData,
                    total:this.total,
                    warningNumber:this.warningNumber,
                    handleNumber:this.handleNumber,
                }
                sessionStorage.setItem('warningData',JSON.stringify(warningData))
            })
        },
        goToDetails(item){
            console.log(item)
            this.$router.push({
                name:'EarlyWarning',
                query:{
                    details:{
                        item,
                        total:this.total,
                        warningNumber:this.warningNumber,
                        handleNumber:this.handleNumber,
                    },
                }
            })
                            
        }
    },
    computed:{
        classOption() {
            return {
            step: .5, // 数值越大速度滚动越快
            // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
            hoverStop: true, // 是否开启鼠标悬停stop
            direction: 1, // 0向下 1向上 2向左 3向右
            // openWatch: true, // 开启数据实时监控刷新dom
            singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
            singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
            waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
            };
        },
    }
};
</script>

<style lang="scss">
@import "@/styles/common.scss";
.early-warning-container {
    .my-table-body{
        overflow: hidden !important;
    }
    .public-title {
        position: relative;
    }
    .early-warning-header {
        display: flex;
        font-size: 21px;
        position: absolute;
        left: 320px;
        top: 14px;
        .box {
            margin-right: 48px;
            white-space:nowrap
        }
        .number {
            color: #ffdc98;
            font-size:35px;
            font-weight: bold;
        }
        .title-warning {
            color: #fff;
            font-size:14px;
        }
        .title-handle {
            color: #7bed4c;
            font-size:14px;
        }
    }
    

    
}
</style>