<template>
  <div class="public-card aside-card">
    <div class="public-title">数据分析</div>
    <div class="btn_box">
      <div style="margin-bottom: 48px; margin-top: 35px">
        <p :class="title== '空气质量分析' ? 'active': '' " @click="detailBtn('空气质量分析')">空气质量分析</p>
        <p :class="title== '地表水断面分析' ? 'active': '' " @click="detailBtn('地表水断面分析')">地表水断面分析</p>
        <p :class="title== '裸地分析' ? 'active': '' " @click="detailBtn('裸地分析')">裸地分析</p>
        <p :class="title== '积尘分析' ? 'active': '' " @click="detailBtn('积尘分析')">积尘分析</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      content:
        "负责制定本区大气污染防治工作计划，并做好督促实施和组织协调；开展大气污染防治调研,提出对策建议；开展大气污染防治监督检查,做好问题反馈和信息联络；编制大气污染防治工作简报和工作报告；负责组织大气污染防治工作会议,并做好督促落实；承担区委生态文明委大气污染综合治理及应对气候变化工作小组的相关工作。承担大兴区空气重污染应急指挥部的具体工作。",
    };
  },
  watch: {
    $route(newVla) {
      // if (newVla.path.indexOf("dataAnalysis") < 0) {
      //   this.title = "";
      // }
      if (newVla.path == "/home") {
        this.title = "";
      }
    },
  },
  methods: {
    detailBtn(title) {
      this.title = title;
      if (title === "空气质量分析") {
        this.$router.push({ name: "DataAnalysis" });
      }

      if (title === "地表水断面分析") {
        this.$router.push({ name: "WaterAnalysis" });
      }

      if (title === "裸地分析") {
        this.$router.push({ name: "NakedAnalysis" });
      }

      if (title === "积尘分析") {
        this.$router.push({ name: "DustAnalysis" });
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "@/styles/common.scss";
.btn_box {
  // width: 572px;
  margin-left: 20px;
  margin-top: 15px;
  p {
    cursor: pointer;
    display: inline-block;
    width: 183px;
    height: 134px;
    background: url("~@/assets/icon01.png") center center no-repeat;

    border-radius: 1px;
    text-align: center;
    line-height: 318px;
    white-space: nowrap;
    font-size: 24px;
    margin-right: 11px;
    color: #9ee5ff;
    &.active {
      background-image: url("~@/assets/icon01-active.png");
    }
    &:nth-child(2) {
      background-image: url("~@/assets/icon02.png");
